import { h } from 'preact';
import TextTruncate from 'react-text-truncate';
import Rating from '../../../Rating';
import './CardDescription.css';
import { generateId } from '../../../../helpers';

const CardDescription = ({
	title,
	description,
	rating,
	reviewsNumber,
	reviews,
	hours
}) => (
	<div class="card-descr">
		<h4 class="card-descr__title">{title}</h4>
		<div class="rating">
			<Rating rating={rating} />
			<span class="rating-reviews">{reviewsNumber} reviews</span>
			<span class="rating-hours">{hours} hours</span>
		</div>
		<div class="description">
			<TextTruncate
				line={3}
				truncateText="..."
				text={description}
			/>
		</div>
		<div class="reviews">
			{
				reviews.map(review => (
					<div class="review-text" key={generateId()}>
						<TextTruncate
							line={1}
							truncateText="..."
							text={review}
						/>
					</div>
				))
			}
		</div>
	</div>
);

export default CardDescription;



