import { h } from 'preact';
import CardList from '../CardList';
import './Container.css';

const Container = ({ data }) => (
	<div class="container">
		<div class="row">
			<div class="col-sm">
				<h3 class="page-title">Destin boating activities</h3>
				<CardList
					cards={data.cards}
				/>
			</div>
		</div>
	</div>
);

export default Container;


