import { h } from 'preact';
import './CardImages.css';

const CardImages = ({ images }) => (
	<div class="card-images">
		<img class="card-img" src={images[0]} alt="" />
		<div class="card-img-small">
			<img class="card-img" src={images[1]} alt="" />
			<img class="card-img" src={images[2]} alt="" />
		</div>
	</div>
);

export default CardImages;



