import { h } from 'preact';
import './CardPermission.css';

const CardPermission = ({ permission }) => (
	<div class="card-permission">
		<span class={`card-permission__icon ${permission}`} />
		<span class="card-permission__text">{permission} Frendly</span>
	</div>
);

export default CardPermission;



