import { h } from 'preact';
import CardPermissions from './CardPermissions';
// import './CardInfo.css';

const CardInfo = ({ price, permissions }) => (
	<div class="card-info">
		<div class="card-price">
			<div class="card-price__text-line">from</div>
			<div class="card-price__currency">
				<span class="card-price__currency-sign">$</span>
				<span>{price}</span>
			</div>
			<div class="card-price__tax">+ tax</div>
		</div>
		<CardPermissions permissions={permissions} />
	</div>
);

export default CardInfo;



