import React from 'react';
import PropTypes from 'prop-types';
import star from '../../assets/star.svg';
import starHalf from '../../assets/star_half.svg';
import starEmpty from '../../assets/star_empty.svg';

const Rating = ({ rating }) => {
  switch (rating) {
    case 1:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={starHalf} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
        </span>
      );
    case 2:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
        </span>
      );
    case 3:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={starHalf} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
        </span>
      );
    case 4:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
        </span>
      );
    case 5:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={starHalf} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
        </span>
      );
    case 6:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
        </span>
      );
    case 7:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={starHalf} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
        </span>
      );
    case 8:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={starEmpty} alt="" />
        </span>
      );
    case 9:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={starHalf} alt="" />
        </span>
      );
    default:
      return (
        <span class="rating-stars">
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
          <img class="rating-star" src={star} alt="" />
        </span>
      );
  }
}

Rating.propTypes = {
  rating: PropTypes.number.isRequired,
}

export default Rating;


