import { h } from 'preact';
import CardImages from './CardImages';
import CardDescription from './CardDescription';
import CardInfo from './CardInfo';
import './Card.css';

const Card = ({ card }) => (
	<div class="card">
		<div class="container">
			<div class="row">
				<div class="col-sm card-images__wrap">
					<CardImages images={card.images} />
				</div>
				<div class="col-sm">
					<CardDescription
						title={card.title}
						description={card.description}
						rating={card.rating}
						reviewsNumber={card.reviewsNumber}
						reviews={card.reviews}
						hours={card.hours}
					/>
				</div>
				<div class="col-sm card-info__wrap">
					<CardInfo
						price={card.price}
						permissions={card.permissions}
					/>
				</div>
			</div>
		</div>
	</div>
);

export default Card;



