import { h } from 'preact';
import { generateId } from '../../helpers';
import Card from './Card';

const CardList = ({ cards }) => (
	<div class="cards">
		{
			cards.map(card =>
				<Card card={card} key={generateId()} />
			)
		}
	</div>
);

export default CardList;



